import Vector from "./vector";
import { rotateXYZmatrix, multiply4x4matrix } from "./geometry";
class NormalUnitCube {
    constructor() {
        this.CENTER = new Vector(0, 0, 0, "cube-center"); // cube-center
        /*
           E4------F5      y
           |`.    | `.     |
           |  `A0-----B1   *----- x (WORLD COORDINATE FRAME)
           |   |  |   |     \
           G6--|--H7  |      \
            `. |   `. |       z
              `C2-----D3
        */
        this.AXES = [
            new Vector(0.25, 0, 0, "xAxis"),
            new Vector(0, 0.25, 0, "yAxis"),
            new Vector(0, 0, 0.25, "zAxis"),
        ];
        this.POINTS = [
            new Vector(-1, +1, +1, "front-top-left"),
            new Vector(+1, +1, +1, "front-top-right"),
            new Vector(-1, -1, +1, "front-bottom-left"),
            new Vector(+1, -1, +1, "front-bottom-right"),
            new Vector(-1, +1, -1, "back-top-left"),
            new Vector(+1, +1, -1, "back-top-right"),
            new Vector(-1, -1, -1, "back-bottom-left"),
            new Vector(+1, -1, -1, "back-bottom-right"),
        ];
        /*
              i0
        g6 *---*---*
           |   |   |
        j1 *---*---* k2
           |   |   |
           *---*---*
              l3
                    i0
               *----*
               |    |
            j1 *----*
                \   |
                 \  * l3
                 m4  \
                      \ n5
        */
        this.CROSS_SECTION_POINTS = [
            new Vector(+0, +1, -1, "top-center"),
            new Vector(-1, +0, -1, "left-center"),
            new Vector(+1, +0, -1, "right-center"),
            new Vector(+0, -1, -1, "bottom-center"),
            new Vector(-1, +0, +1, "left-center-forward"),
            new Vector(+0, -1, +1, "bottom-center-forward"),
        ];
    }
}
const UNIT_CUBE = new NormalUnitCube();
class SceneCube {
    constructor(euler, worldWrtCameraMatrix, zOffset, range, projectionConstant) {
        this.center3d = UNIT_CUBE.CENTER;
        this.axes3d = UNIT_CUBE.AXES;
        this.vertexPoints3d = UNIT_CUBE.POINTS;
        this.crossPoints3d = UNIT_CUBE.CROSS_SECTION_POINTS;
        this.wrtWorldMatrix = rotateXYZmatrix(euler);
        this.wrtCameraMatrix = multiply4x4matrix(worldWrtCameraMatrix, this.wrtWorldMatrix);
        this.zOffset = zOffset;
        this.range = range;
        this.projectionConstant = projectionConstant;
        this.crossPoints2d = this._projectedPoints(this.crossPoints3d);
        this.vertexPoints2d = this._projectedPoints(this.vertexPoints3d);
        const [center2d] = this._projectedPoints([this.center3d]);
        this.center2d = center2d;
        this.axes2d = this._projectedPoints(this.axes3d);
        this.worldAxes2d = this._projectedPoints(this.axes3d, 0, worldWrtCameraMatrix);
        const [worldOrigin2d] = this._projectedPoints([this.center3d], 0, worldWrtCameraMatrix);
        this.worldOrigin2d = worldOrigin2d;
    }
    _projectedPoints(points, offset = this.zOffset, transformMatrix = this.wrtCameraMatrix) {
        return points.map((point) => {
            return new Vector(point.x, point.y, point.z + offset, point.name)
                .transform(transformMatrix)
                .project(this.projectionConstant);
        });
    }
}
export default SceneCube;
