import { DataSpecType, } from "./primitive-types";
/*
   E4------F5      y
   |`.    | `.     |
   |  `A0-----B1   *----- x
   |   |  |   |     \
   G6--|--H7  |      \
    `. |   `. |       z
      `C2-----D3
face 1 - A0, B1, D3 | C2 (front)
face 2 - B1, F5, H7 | D3 (front right)
face 3 - F5, E4, G6 | H7 (front left)
face 4 - E4, A0, C2 | G6 (back)
face 5 - E4, F5, B1 | A0 (top)
face 6 - C2 , D3, H7 | G6 |(bottom)
*/
const POINT_FACE_SET = [
    [0, 1, 3, 2],
    [1, 5, 7, 3],
    [5, 4, 6, 7],
    [4, 0, 2, 6],
    [4, 5, 1, 0],
    [2, 3, 7, 6],
];
const drawAxis = (p, v, color, opacity, size, name, axisType) => ({
    x0: [p.x],
    y0: [p.y],
    x1: [v.x],
    y1: [v.y],
    color,
    opacity,
    size,
    type: DataSpecType.lines,
    id: `${axisType}-${name}`,
});
const drawAxes = (p0, vx, vy, vz, name, AxesOptions) => {
    const { intersectionPointColor, intersectionPointSize, xColor, yColor, zColor, lineSize, edgeOpacity, } = AxesOptions;
    let opacity = edgeOpacity || 1;
    let size = lineSize || 1;
    const xAxis = drawAxis(p0, vx, xColor, opacity, size, name, "x");
    const yAxis = drawAxis(p0, vy, yColor, opacity, size, name, "y");
    const zAxis = drawAxis(p0, vz, zColor, opacity, size, name, "z");
    const centerPoint = {
        x: [p0.x],
        y: [p0.y],
        color: intersectionPointColor,
        opacity: 1.0,
        size: intersectionPointSize || 3,
        type: DataSpecType.points,
        id: `point-${name}`,
    };
    return [xAxis, yAxis, zAxis, centerPoint];
};
class SceneCubeRenderer {
    constructor(cube, sceneOptions) {
        this.cube = cube;
        this.sceneOptions = sceneOptions;
    }
    render() {
        return [
            ...this.drawXYplane(),
            ...this.drawBox(),
            ...this.drawCrossSectionLines(),
            ...this.drawEdgeAxes(),
            ...this.drawWorldAxes(),
            ...this.drawCubeAxes(),
        ];
    }
    /*
   E4------F5      y
   |`.    | `.     |
   |  `A0-----B1   *----- x
   |   |  |   |     \
   G6--|--H7  |      \
    `. |   `. |       z
      `C2-----D3
face 1 - A0, B1, D3 | C2 (front)
face 2 - B1, F5, H7 | D3 (front right)
face 3 - F5, E4, G6 | H7 (front left)
face 4 - E4, A0, C2 | G6 (back)
face 5 - E4, F5, B1 | A0 (top)
face 6 - C2 , D3, H7 | G6 |(bottom)
*/
    drawBox() {
        const { sceneEdges } = this.sceneOptions;
        if (!sceneEdges) {
            return [];
        }
        const start = [0, 1, 3, 2, 5, 7, 6, 4, 1, 0, 3, 2];
        const end = [1, 3, 2, 0, 7, 6, 4, 5, 5, 4, 7, 6];
        const p = this.cube.vertexPoints2d;
        const { color, opacity } = sceneEdges;
        const lines = {
            x0: start.map(i => p[i].x),
            y0: start.map(i => p[i].y),
            x1: end.map(i => p[i].x),
            y1: end.map(i => p[i].y),
            color: color,
            opacity: opacity || 1.0,
            size: 1,
            type: DataSpecType.lines,
            id: "scene-edges-cube-box",
        };
        return [lines];
    }
    /*
E4              y
|               |
|               *----- x (WORLD COORDINATE FRAME)
|                \
G6-------H7       \
 \                z
  \C2
  xEdge=red
  yEdge=blue
  zEdge=green
  intersectionPoint=white (G6)
*/
    drawEdgeAxes() {
        const { edgeAxes } = this.sceneOptions;
        if (!edgeAxes) {
            return [];
        }
        const p = this.cube.vertexPoints2d;
        return drawAxes(p[6], p[7], p[4], p[2], "edge-axes", edgeAxes);
    }
    drawWorldAxes() {
        const { worldAxes } = this.sceneOptions;
        if (!worldAxes) {
            return [];
        }
        const v = this.cube.worldAxes2d;
        const p_ = this.cube.worldOrigin2d;
        return drawAxes(p_, v[0], v[1], v[2], "world-axes", worldAxes);
    }
    drawCubeAxes() {
        const { cubeAxes } = this.sceneOptions;
        if (!cubeAxes) {
            return [];
        }
        const v = this.cube.axes2d;
        const p_ = this.cube.center2d;
        return drawAxes(p_, v[0], v[1], v[2], "cube-axes", cubeAxes);
    }
    /*
/*
E4          F5
 *----------*
 |          |
 |          |    y
 |          |    |
 *----------*    *-- x
 G6         H7
*/
    drawXYplane() {
        const { xyPlane } = this.sceneOptions;
        if (!xyPlane) {
            return [];
        }
        const { color, opacity } = xyPlane;
        const p = this.cube.vertexPoints2d;
        const polygon = {
            x: [p[4].x, p[5].x, p[7].x, p[6].x],
            y: [p[4].y, p[5].y, p[7].y, p[6].y],
            fillColor: color,
            fillOpacity: opacity || 1.0,
            borderColor: color,
            borderOpacity: opacity || 1.0,
            borderSize: 1,
            type: DataSpecType.polygon,
            id: "xy-plane",
        };
        return [polygon];
    }
    /*
                          (y)^
          i0                 |
       *---*---*          +1 |
       |   |   |           0 |---------->(x)
    j1 *---*---* k2       -1 | -1, 0, 1
       |   |   |
       *---*---*
          l3
                i0
           *----*
           |    |
        j1 *----*
            \   |
             \  * l3
             m4  \
                  \ n5
         E4          F5
          *----------*
          |          |
          |          |    y
          |          |    |
          *----------*    *-- x
          G6         H7
    */
    drawCrossSectionLines() {
        const { crossLines } = this.sceneOptions;
        if (!crossLines) {
            return [];
        }
        const p = this.cube.crossPoints2d;
        const t = this.cube.vertexPoints2d;
        const { color, opacity } = crossLines;
        const lines = {
            x0: [p[0].x, p[1].x, p[1].x, p[3].x, t[6].x, t[4].x, t[5].x, t[7].x],
            y0: [p[0].y, p[1].y, p[1].y, p[3].y, t[6].y, t[4].y, t[5].y, t[7].y],
            x1: [p[3].x, p[2].x, p[4].x, p[5].x, t[4].x, t[5].x, t[7].x, t[6].x],
            y1: [p[3].y, p[2].y, p[4].y, p[5].y, t[4].y, t[5].y, t[7].y, t[6].y],
            color: color,
            opacity: opacity || 1.0,
            size: 1,
            type: DataSpecType.lines,
            id: "cross-section-lines",
        };
        return [lines];
    }
}
export default SceneCubeRenderer;
